/* ************************
   02.2: Animations
   ********************* */
.socials {
  a {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    background-color: #fff6f1;
    &:hover {
      @extend .c2-bg;
    }
    &:not(:last-child) {
      margin-right: 6px;
      @include mobileXs {
        margin-right: 0;
      }
    }
  }
}
